// var cartGrey = #8E8E8E;

body {
  background-color: #F4F5F0;
  width: 100%;
}

ul {
  list-style: none;
}
li {
  display: inline-block;
  font-size: 12px;
  margin-right: 2%;
}
nav {
  background: white;
  margin: 0;
  padding: 0;
}

.nav1 {
  background-color: white;
  width: 95%;
  padding: 1% 3%;
  overflow: auto;
  border-bottom: 2px solid lightgrey;

}
.nav1 h1{
  color: #F46200;
  // margin: 0;
  // padding: 3% .25% 3% 5%;
  width: 20%;
  display: inline-block;
  font-family: serif;
}

.nav1leftside {
  // background-color: white;
  float: left;
  width: 60%;
}

input {
  width: 50%;
  border-radius: .5em 0 0 .5em;
  background: #FBFBFA;
  // .placeholder {
  //   font-size: 12px;
  // }
}
.nav1rightside {
  color: #3CA7C2;
  float: right;
  // display: inline-block;
  // background-color: white;
  width: 40%;
  // padding: 0  0 .5% 0;
}

.nav1leftside button {
  color: white;
  background-color: #3CA7C2;
  border-radius: 0 .5em .5em 0;
  border-style: none;
}


.shopping {
  float: right;
  width: 10%;
  padding: 3% 5% 3% 2%;
  color: #8E8E8E;
  cursor: pointer;
  border-left: 2px solid lightgrey;
}

.shopping p {
  margin: 0;
}
// .shopping p:hover {
//   color: solid black;
// }

.nav1rightside {
  margin-top: 2%;
}

.nav1rightside button {
  background-color: white;
  color: #3CA7C2;
  border-color: #3CA7C2;
  border-radius: .5em;
  width: 20%;
  // height: 10%;
  font-size: 10px;
  // margin-right: 1%;
}


hr {
  margin: 0;
  padding: 0;
  border-color: solid #E4E5E0;
  font-style: serif;
}

.nav2 {
  clear: both;
  border-bottom: 2px solid lightgrey;
}
.nav2 ul{
  color: #797979;
  display: inline-block;
  width: 100%;
}

h5 {
  // margin-top: 22%;
  margin-bottom: 8%;
}

.aside {
  clear: both;
  width: 17%;
  // background-color: #F46200;
  margin-left: 3%;
  display: inline-block;
}

.aside h5 {
  color: #545453;
}

.aside a {
  color: #3CA7C2;
  // margin-right: 30%;
  font-size: .75em;
  text-decoration: none;
  display: block;
  padding-bottom: 4%;
  a:hover {
    text-decoration: underline;
  }
}

.categories {
  background-color: white;
  border: 1px solid grey;
  border-radius: .25em;
}

.categories h5 {
  margin: 8% 0 0% 7%;
}

.catlist {
  padding-left: 12%;
  margin: 3% 0 5% 0;
}

.container {
  width: 74%;
  float: right;
  margin: 0 3% 0 3%;
  clear: both;
}

.merchHead {
  margin-top: 3%;
}
.merchHeadLeft h5 {
  float: left;
  margin-top: 1%;
  margin-bottom: 1.5%

}
.merchHeadRight h5 {
  float: right;
  margin-top: 1%;
  margin-bottom: 1.5%
}

.merchContainer {
  clear: both;
}
section {
  background-color: white;
  width: 32%;
  margin-bottom: 2%;
  margin-right: .5%;
  font-size: .75em;
  display: inline-block;
  border-radius: .25em;
}

section p {
  margin: 0;
}

.price {
  color: #79BE3B;
}
.seller {
  color: #8E8E8E;
}
.underline {
  text-decoration: underline;
}

.footer .footNav {
  color: #3CA7C2;
}

.pageSearch li {
  border: 1px;
  background-color: white;
  margin: 0;
  padding: 1.5%;
  border: 1px solid lightgrey;
  float: left;
}
